.footer{
  padding: 3em 0;
  background: linear-gradient(0deg, #FFF 0%, #FFF 100%), #1F1F1F;
  color: $darkGray;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    font-weight: 700;
    font-size: 1.875em;
    line-height: 120%;
  }
  &__requisite{
    margin-top: .875em;
    @include grid100gap(.3125em);
    &-desc{
      line-height: 140%;
    }
  }
  &__links,
  &__links2{
    margin-top: 1.5em;
    display: flex;
    flex-direction: column;
  }
  &__link2{
    font-weight: 700;
    @include lastM(0 0 .1875em);
    span{
      font-size: .9375em;
      line-height: 140%;
    }
  }
  &__subtitle{
    margin-bottom: 1.5625em;
  }
  &__link{
    font-size: 1em;
    line-height: 119%;
    @include lastM(0 0 .6875em);
  }
}

.socials{
  margin-top: 1em;
  display: flex;
  align-items: center;
  &__item{
    width: 1.375em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
  }
}

.footer-contacts{
  @include grid100gap(1.1875em);
  &__item{
    display: flex;
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.3125em);
  }
  &__title{
    font-size: .875em;
    line-height: 121%;
  }
  &__desc{
    font-weight: 700;
    font-size: 1.125em;
    line-height: 117%;
  }
}

.footer-info{
  @include grid100gap(1.25em);
  &__item{
    @include grid100gap(.3125em);
  }
  &__desc{
    font-weight: 700;
  }
}

.footer-pay{
  margin-top: .1875em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - .1875em));
  gap: .25em;
  max-width: 9.875em;
  &__item{
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.75em 0 8em;
    &__inner{
      justify-content: unset;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1.875em));
      gap: 3.75em;
    }
    &__subtitle{
      span{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .footer-pay{
    max-width: 14.875em;
    grid-template-columns: repeat(6,1fr);
  }
}
