.text60{
  font-size: 3.75em;
  line-height: 120%;
}

.text45{
  font-size: 2.8125em;
  line-height: 120%;
}

.text40{
  font-size: 2.5em;
  line-height: 120%;
}

.text30{
  font-size: 1.875em;
  line-height: 130%;
}

.text24{
  font-size: 1.5em;
  line-height: 121%;
}


.text p,
.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text18{
  font-size: 1.125em;
  line-height: 130%;
}

.text16{
  font-size: 1em;
  line-height: 140%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 121%;
}

.text{
  p{
    @include lastM(0 0 1em);
  }
}


.bold-text{
  font-weight: 700;
}

.text-underline{
  text-decoration: underline;
}

.dark-text{
  color: $dark;
}

.dark-text70{
  color: rgba($dark,.7);
}

.dark-text80{
  color: rgba($dark,.8);
}

.primary-text{
  color: $primary;
}

.white50-text{
  color: rgba(#616161,.5);
}

.white60-text{
  color: rgba(#616161,.6);
}


@media screen and (max-width: 992px){
  .text60{
    font-size: 1.875em;
  }

  .text45{
    font-size: 1.25em;
    line-height: 125%;
  }

  .text40{
    font-size: 1.5em;
  }

  .text24{
    font-size: 1em;
  }

  .text20{
    font-size: .9375em;
    line-height: 140%;
  }

  .text16,
  .text15{
    font-size: .875em;
    line-height: 140%;
  }

  .mob-text25{
    font-size: 1.5625em;
  }
}
