.blog-main{
  color: #fff;
  position: relative;
  &__inner{
    padding: 5.5em 0;
    min-height: 39.75em;
    display: flex;
    align-items: flex-end;
    position: relative;
    z-index: 2;
  }
  &__content{
    min-height: 9em;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    ._view2{
      display: none;
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
}

.blog-collection{
  padding: 2.5em 0 3.75em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
    margin-top: 2.5em;
  }
}

.m-pagination{
  display: flex;
  margin-top: 2.875em;
  &__item{
    width: 3.125em;
    height: 3.125em;
    border-radius: .25em;
    @include center;
    font-weight: 700;
    color: rgba($black,.4);
    &._active,
    &:hover{
      background-color: $primary;
      color: #fff;
    }
    &._dots{
      pointer-events: none;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.m-article{
  padding: 5em 0 2.1875em;
  &__img{
    position: relative;
    img{
      width: 100%;
      display: block;
    }
    &-text{
      position: absolute;
      right: 0;
      bottom: 0;
      padding: 0 2.5em;
      height: 4.625em;
      background-color: #fff;
      color: $dark;
      @include center;
      span{
        font-size: 1.25em;
        line-height: 125%;
      }
    }
  }
  &__cards{
    margin: 3.125em 0;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__video{
    padding-bottom: 56%;
    position: relative;
    iframe{
      top: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      display: block;
    }
  }
  .new-question{
    margin-top: 4.375em;
  }
}

.article-card{
  &__img{
    width: 3.5em;
    height: 3.5em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__info{
    margin-top: 1.5625em;
    @include grid100gap(.3125em);
  }
}

.text{
  margin-top: 1.25em;
  max-width: 60.25em;
  h3{
    font-size: 2.1875em;
    line-height: 120%;
    font-weight: 700;
    @include lastM(0 0 .57em);
  }
  p{
    font-size: 1.25em;
    line-height: 130%;
    color: $dark;
    @include lastM(0 0 1em);
  }
  hr{
    height: 3.75em;
    display: block;
    border: none;
  }
}

.about-blog{
  padding-bottom: 2em;
}


@media screen and (max-width: 992px){
  .blog-main{
    &__inner{
      min-height: 33.75em;
      padding: 1.875em 0;
    }
    &__content{
      min-height: 6.75em;
    }
  }

  .blog-collection{
    padding: 3.75em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      margin-top: 1.625em;
    }
  }

  .m-pagination{
    margin-top: 2.625em;
    width: calc(100% + 3.125em);
    margin-left: -1.5625em;
    justify-content: center;
  }

  .m-article{
    .text45{
      font-size: 1.5625em;
    }
    &__img{
      &-text{
        height: 2.5em;
        padding: 0 1em;
        span{
          font-size: .875em;
        }
      }
    }
    &__cards{
      margin: 2.5em 0;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
    }
    .new-question{
      margin-top: 3.75em;
    }
  }

  .article-card{
    .text20{
      font-size: 1em;
    }
  }

  .text{
    h3{
      font-size: 1.5625em;
      @include lastM(0 0 .8em);
    }
    p{
      font-size: .9375em;
    }
    hr{
      height: 2.5em;
    }
  }

  .about-blog{
    padding-bottom: 0;
  }
}


@media screen and (max-width: 576px){
  .blog-main{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .blog-collection{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .m-article{
    &__cards{
      grid-template-columns: 100%;
    }
  }
}
