.yoomoney{
  padding: 5em 0;
  &__img{
    width: 4.25em;
    display: block;
    margin-bottom: 2.375em;
  }
  &__desc{
    margin-top: 1.25em;
    span{
      color: $primary;
      font-weight: 700;
      white-space: nowrap;
    }
  }
  &__card{
    display: inline-flex;
    padding: .625em;
    white-space: nowrap;
    margin-top: 1.25em;
    border: 1px dashed rgba(0, 0, 0, 0.33);
    border-radius: .3125em;
    color: $primary;
    span{
      text-decoration-line: underline;
    }
  }
  &__desc2{
    max-width: 42.25em;
    margin-top: 2.875em;
  }
}

.yoomoney-form{
  margin-top: 3em;
  @include grid100gap(1.125em);
  max-width: 29.5em;
  .main__btn{
    position: static;
    margin: 0;
  }
}

.file-input{
  position: relative;
  height: 4.375em;
  padding: 0 1.375em;
  border: 1px solid rgba(0, 0, 0, 0.55);
  border-radius: .3125em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__wrap{
    display: flex;
    align-items: center;
    color: rgba($black,.5);
    svg{
      width: 1.5625em;
      height: auto;
      margin-right: .625em;
    }
  }
  &__btn{
    //position: relative;
    cursor: pointer;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 3;
      cursor: pointer;
      opacity: 0;
      display: block;
    }
    span{
      pointer-events: none;
    }
  }
}


@media screen and (max-width: 992px){
  .yoomoney{
    padding: 2.25em 0;
    &__img{
      display: none;
    }
    &__desc,
    &__desc2{
      margin-top: 1.375em;
    }
  }

  .yoomoney-form{
    margin-top: 1.75em;
    gap: .5em;
    .text30{
      display: none;
    }
    .main__btn{
      height: 3.75em;
    }
  }

  .file-input{
    height: 3.75em;
    &__wrap{
      svg{
        width: 1.125em;
      }
      span{
        font-size: 1em;
      }
    }
  }
}
