.modal{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    &__bg {
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
        background: rgba(255, 255, 255, 0.44);
        backdrop-filter: blur(16.5px);
    }
    &--active {
        opacity: 1;
        pointer-events: unset;
        .product-modal__bg,
        .modal__bg {
            display: block;
        }
    }
    &__close {
        position: fixed;
        top: 0;
        right: 0;
        width: 4em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__dialog{
        position: relative;
        z-index: 3;
        width: 40em;
        max-height: calc(100 * var(--vh));
        overflow-y: auto;
    }
}

.modal2{
    .modal__bg{
        background: #414141;
        opacity: .7;
    }
    .modal__close{
        position: absolute;
        top: 0;
        left: 100%;
        right: unset;
        margin-left: 3em;
        width: 3.25em;
        height: 3.25em;
        background-color: #fff;
        border-radius: 50%;
        box-shadow: 0 .6875em 2.0625em rgba(0, 0, 0, 0.25);
        svg{
            width: 1em;
            height: auto;
        }
        &:hover{
            background-color: $primary;
            path{
                stroke: #fff;
            }
        }
    }
    .modal__dialog{
        position: relative;
        background: #FFFFFF;
        border-radius: .3125em;
        width: 60em;
        max-width: calc(100% - 1.25em);
        text-align: center;
        max-height: unset;
        overflow: unset;
    }
    .modal__scroll{
        padding: 2.375em 2em 2.875em;
        max-height: calc(100 * var(--vh));
        overflow-y: auto;
    }
}


.login-modal,
.pay-modal{
    &__img{
        margin: 1.6875em auto 2em;
        width: 100%;
        max-width: 33.75em;
    }
    .text{
        max-width: 47.5em;
        margin: 0 auto;
    }
}

.pay-modal{
    &__img{
        max-width: 40em;
    }
    .text{
        margin-top: 2em;
    }
}


@media screen and (max-width: 992px){
    .modal2{
        padding-top: 2em;
        .modal__close{
            left: unset;
            right: 0;
            transform: translateY(-50%);
            margin: 0;
        }
        .modal__dialog{
            max-height: 100%;
        }
        .modal__scroll{
            padding: 3.875em 0;
        }
    }

    .pay-modal,
    .login-modal{
        &__img{
            margin: 1.125em 0;
        }
        .text{
            padding: 0 2em;
        }
    }

    .pay-modal{
        .text{
            margin-top: 1.125em;
        }
    }
}
