.header{
  z-index: 20;
  &._type2{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    .fil1,
    .path-fill{
      fill: #fff !important;
    }
    .header__inner{
      border-color: rgba(#FFFFFF,.4);
    }
    .header__support:not(:hover){
      border-color: rgba(255, 255, 255, 0.44);
      color: #fff;
    }
    .header__partners{
      color: #fff;
    }
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 1.3125em 0 1.125em;
    border-bottom: 1px solid rgba($black,.2);
  }
  &__logo{
    display: block;
    width: 16.3705em;
    margin-right: auto;
    svg,
    img{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: .875em;
    ._view2{
      display: none;
    }
  }
  &__partners{
    font-weight: 700;
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
  &._fixed-header{
    box-shadow: 0 0 .625em rgba(123, 152, 196, .3);
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    border: none;
    .header__inner{
      border: none;
    }
    &._type2{
      background-color: $black;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: .4375em 0 .875em;
    border-bottom: 1px solid rgba($black,.2);
    &._type2{
      border-color: rgba(#FFFFFF,.4);
    }
    &__inner{
      border: none;
      padding: 0;
    }
    &__partners{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 9.5em;
    }
    &__btn{
      margin-left: 1.125em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__support{
      border: none;
      padding: 0;
      height: unset;
      svg{
        margin: 0;
      }
      span{
        display: none;
      }
      &:hover{
        background-color: transparent;
        path{
          fill: $primary;
        }
      }
    }
  }
}
