.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid rgba($black, 0.55);
    border-radius: .5em;
    padding: 0 1em;
    height: 3.4em;
    width: 100%;
    font-size: 1.25em;
    line-height: 120%;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: rgba($black, .5);
    }
    &:focus {
      border-color: $primary;
    }
  }
  &__icon,
  &__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  &__icon{
    left: 1.375em;
    &~input{
      padding-left: 2.8em;
    }
  }
  &__btn{
    right: 1.275em;
    &~input{
      padding-right: 9em;
    }
  }
  &__error-text{
    position: absolute;
    top: 100%;
    left: 0;
    margin-top: .0625em;
    color: rgba(255, 0, 0, 0.55);
    opacity: 0;
    pointer-events: none;
    span{
      font-size: .875em;
    }
  }
  &._error{
    margin-bottom: .75em;
    input{
      border-color: rgba(255, 0, 0, 0.55);
      &::placeholder{
        color: transparent;
      }
    }
    .fg__error-text{
      opacity: 1;
    }
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        border: none;
        background-image: url(../img/check4.svg);
      }
    }
  }
  label {
    padding-left: 2em;
    position: relative;
    min-height: 1.125em;
    font-size: 1em;
    line-height: 120%;
    cursor: pointer;
    display: block;
    color: rgba($dark,.55);
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: .3125em;
      border: 1px solid rgba($black, 0.55);
      width: 1.125em;
      height: 1.125em;
      left: 0;
      top: 0;
      transition: all .100s;
      @include bgImgSize(100%);
    }
    a{
      position: relative;
      z-index: 2;
      font-weight: 700;
      text-decoration: underline;
      &:hover{
        color: $dark;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input{
      font-size: .875em;
      height: 3.58em;
    }
    &__icon{
      left: .625em;
      &~input{
        padding-left: 3.14em;
      }
    }
    &__btn{
      right: .625em;
      &~input{
        padding-right: 10em;
      }
    }
  }
}
