.success{
  padding: 10em 0;
  &__inner{
    text-align: center;
    max-width: 40em;
    margin: 0 auto;
    @include grid100gap(1.625em);
  }
  &__check{
    width: 5.625em;
    display: block;
    margin: 0 auto;
  }
  a{
    text-decoration: underline;
    color: $blue;
  }
}

.error404{
  flex-grow: 1;
  padding: 2em 0 7em;
  &__inner{
    width: 60.875em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__img{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
  &__text{
    margin: -2em auto 0;
    text-align: center;
    max-width: 20em;
    font-size: 2em;
    line-height: 144%;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.25em;
  }
  &__link{
    padding: 0 1.5625em;
    height: 4.625em;
    span{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .success{
    padding: 8.5em 0;
    .text45{
      font-size: 1.5625em;
    }
  }

  .error404{
    padding: 4.75em 0;
    &__img{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__text{
      font-size: 1.25em;
      margin-top: .42em;
      opacity: .7;
    }
    &__nav{
      margin-top: 1.625em;
    }
    &__link{
      padding: 0;
      width: 21.25em;
      max-width: 100%;
      height: 4em;
      span{
        font-size: 1em;
      }
    }
  }
}
