.top-up{
  padding: 2.62em 0 3.87em;
  &__wrap{
    border-radius: 1.375em;
    background: radial-gradient(50% 50.00% at 50% 50.00%, rgba(255, 255, 255, 0.55) 0%, rgba(255, 255, 255, 0.00) 100%), rgba(255, 255, 255, 0.96);
    box-shadow: 0 1.375em 4.8125em 0 rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(22px);
    padding: 3.45em 2.21em 4.25em;
    width: calc(100% + 4.42em);
    margin-left: -2.21em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
    margin-top: 3.2em;
  }
  &__fields{
    max-width: 29.5em;
    margin-top: 1.16em;
    @include grid100gap(.8125em);
  }
  &__check{
    margin-top: 1.38em;
  }
  &__btn{
    width: 100%;
    height: 4.25em;
    margin-top: 1.2em;
    span{
      font-size: 1.25em;
    }
  }
}

.sum-fields{
  display: flex;
  width: calc(100% + .3em);
  margin-top: .9em;
}

.sum-field{
  margin: .3em .3em 0 0;
  position: relative;
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    &:checked+label{
      background: $gradient;
      border-color: transparent;
      color: #fff;
    }
  }
  label{
    font-size: 1em;
    @include center;
    padding: 0 1.18em;
    height: 5.125em;
    border-radius: 0.6875em;
    border: 1px solid rgba(0, 0, 0, 0.55);
    color: rgba(#000,.5);
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
  }
}

.pay-detail{
  margin-top: 3.125em;
  @include grid100gap(1em);
  padding-bottom: 1em;
  border-bottom: 1px solid rgba($dark,.2);
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #545454;
    &--last{
      padding-top: 1em;
      border-top: 1px solid rgba($dark,.2);
      font-weight: 700;
      .pay-detail__title,
      .pay-detail__desc{
        font-size: 1.5em;
        line-height: 121%;
      }
    }
  }
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 117%;
  }
}

.benefit{
  padding: 3.87em 0;
  &__items{
    margin-top: 2.14em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.15em 2.5em;
  }
}

.benefit-info{
  @include grid100gap(1.42em);
  max-width: 46em;
}

._card{
  background: #FFFFFF;
  box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
  border-radius: .6875em;
  padding: 1.25em;
}

.benefit-card{
  display: grid;
  grid-template-columns: 1.375em calc(100% - 2em);
  gap: .625em;
  background: $gradient;
  color: #fff;
  padding-right: 1em;
  &__icon{
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.625em);
  }
  &__title{
    line-height: 140%;
  }
}

.faq{
  padding: 3.8em 0;
  background-color: rgba(#eee,.5);
  &__items{
    margin-top: 2.1875em;
    @include grid100gap(1.25em);
  }
}

.faq-card{
  &._active{
    .qa-card__btn{
      span{
        transform: rotate(-45deg);
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover{
      cursor: pointer;
    }
  }
  &__btn{
    width: 2.0625em;
    height: 2.75em;
    @include center;
    font-weight: 700;
    span{
      font-size: 1.25em;
      line-height: 125%;
      transition: all .3s;
    }
    &:hover{
      color: $primary;
    }
  }
  &__hidden{
    display: none;
    padding-top: .625em;
  }
}

.new-question{
  display: flex;
  align-items: center;
  margin-top: 2.1875em;
  &__icon{
    width: 2em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: auto;
    flex-shrink: 0;
  }
}

.feedback,
.blog{
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 2.5em;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      position: static;
      margin: 0 0 0 .625em;
      background: #FFFFFF;
      box-shadow: 0 .6875em 3.4375em rgba(0, 0, 0, 0.12);
      border-radius: .3125em;
      width: 3.75em;
      height: 2.8125em;
      svg{
        width: 1.875em;
        height: auto;
      }
      &:hover{
        background: $primary;
        path{
          fill: #fff;
        }
      }
    }
  }
}

.blog{
  padding: 4.0625em 0 5.75em;
  .swiper{
    &-container{
      width: calc(100% + 2.5em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 2.5em;
    }
  }
  &__bottom{
    display: flex;
    margin-top: 2.125em;
  }
  &__btn{
    width: 22em;
    max-width: 100%;
    height: 3.5em;
    span{
      font-size: 1.25em;
    }
  }
}

.feedback{
  padding: 3.87em 0 6.5em;
  .swiper{
    &-slide{
      width: 16.25em;
      padding-right: 1.25em;
    }
  }
  &__text{
    margin-top: 3em;
    color: rgba($dark,.9);
    p,li{
      font-size: 1.125em;
      line-height: 130.5%;
      @include lastM(0 0 .5em);
    }
    ul{
      padding-left: .67em;
      li{
        padding-left: .67em;
        position: relative;
        &:before{
          content: '';
          position: absolute;
          left: 0;
          top: .35em;
          width: .17em;
          height: .17em;
          background-color: rgba($dark,.9);
          border-radius: 50%;
        }
      }
    }
  }
}

.feedback-card{
  &__img{
    @include imgCover(195.8%);
    img{
      border-radius: 1.375em;
      display: block;
    }
  }
}

.news-card{
  height: 100%;
  display: flex;
  flex-direction: column;

  &__img{
    display: block;
    @include imgCover(73%);
    img{
      border-radius: .375em .375em 0 0;
    }
  }
  &__content{
    border-radius: 0 0 .375em .375em;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__info{
    @include grid100gap(.875em);
  }
  &__title{
    line-height: 125%;
  }
  &__desc{
    line-height: 130%;
  }
  &__nav{
    padding-top: .875em;
    margin-top: auto;
    display: flex;
  }
}

.about-us{
  padding: 2.5em 0 1.5em;
  background-color: rgba(#eee,.5);
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
  }
  .text{
    max-width: 34em;
  }
  &__video{
    video{
      width: 100%;
      border-radius: 0.6875em;
    }
  }
}

.pay-other{
  padding: 4.65em 0 4.325em;
  color: #fff;
  &__inner{
    padding: 3.25em 2.5em;
    box-shadow: 0 .6875em 3.4375em 0 rgba(0, 0, 0, 0.12);
    border-radius: 0.6875em;
    position: relative;
  }
  &__content{
    max-width: 22em;
    position: relative;
    z-index: 2;
    @include grid100gap(1em);
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      border-radius: .6875em;
      object-fit: cover;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 2.53em;
    height: 4.25em;
    span{
      font-size: 1.25em;
    }
  }
}



@media screen and (max-width: 992px){
  .top-up{
    padding: 2.64em 0 2.5em;
    &__wrap{
      width: 100%;
      margin: 0;
      padding: 0;
      box-shadow: unset;
      background-color: transparent;
      backdrop-filter: unset;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.86em;
      margin-top: 1.5em;
    }
  }

  .pay-detail{
    margin-top: 1.75em;
    padding-bottom: 0;
    border: none;
    &__row{
      &--last{
        .pay-detail__title,
        .pay-detail__desc{
          font-size: 1.25em;
          line-height: 120%;
        }
      }
    }
    &__title,
    &__desc{
      font-size: .9375em;
      line-height: 121%;
    }
  }

  .benefit{
    padding: 2.5em 0 2em;
    &__items{
      grid-template-columns: repeat(2,calc(50% - .4em));
      gap: .8em;
      margin-top: 2em;
    }
  }

  .benefit-info{
    gap: .625em;
    .text45{
      max-width: 11em;
    }
  }

  .benefit-card{
    &__title{
      font-size: 1em;
    }
  }

  .faq{
    padding: 2.125em 0 2.25em;
    &__items{
      gap: .9375em;
      margin-top: 1.625em;
    }
  }

  .faq-card{
    &__title{
      font-size: 1em;
      line-height: 130%;
    }
  }

  .blog,
  .feedback{
    .swiper{
      &-button{
        width: 2.87738em;
        height: 2.158em;
        border-radius: 0.23975em;
        svg{
          width: 1.43869em;
        }
      }
    }
  }

  .blog{
    padding: 2.25em 0 4.5em;
    .text45{
      font-size: 1.5625em;
    }
    &__slider{
      margin-top: 1.375em;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 20em;
        padding-right: 1.25em;
      }
    }
    &__bottom{
      margin-top: 1.25em;
    }
  }

  .feedback{
    padding: 2em 0 3.6em;
    &__slider{
      margin-top: .75em;
    }
    .swiper{
      &-slide{
        width: 8.75em;
        padding-right: .625em;
      }
    }
    &__text{
      margin-top: 2em;
      p,li{
        font-size: .9375em;
      }
    }
  }

  .feedback-card{
    &__img{
      img{
        border-radius: .76em;
      }
    }
  }

  .news-card{
    &__img{
      padding-bottom: 80%;
    }
  }

  .about-us{
    padding: 2.4em 0;
    &__inner{
      position: relative;
      padding-top: 2.6em;
      gap: 1.35em;
      grid-template-columns: 100%;
    }
    &__col:nth-child(2){
      order: -1;
    }
    .text45{
      font-size: 1.5em;
      position: absolute;
      top: 0;
      left: 0;
    }
    .text{
      margin-top: 0;
    }
  }

  .pay-other{
    padding: 4.65em 0 2.12em;
    &__inner{
      padding: 1.25em;
    }
    &__content{
      gap: .625em;
    }
    &__btn{
      padding: 0 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .top-up{
    &__fields{
      gap: .6em;
      margin-top: .71em;
    }
    &__check{
      label{
        font-size: .7em;
      }
    }
  }

  .sum-fields{
    width: calc(100% + .14em);
    margin-top: .7em;
  }

  .sum-field{
    margin-right: .14em;
    label{
      height: 3.12em;
      padding: 0 .8em;
      border-radius: 0.41913em;
      span{
        font-size: .762em;
      }
    }
  }

  .pay-detail{
    gap: .75em;
  }

  .benefit{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .new-question{
    flex-direction: column;
    text-align: center;
    margin-top: 2.75em;
    &__icon{
      margin: 0 0 .9375em;
    }
    &__btn{
      margin: .9375em auto 0;
      height: 2.375em;
      span{
        font-size: .9375em;
      }
    }
  }

  .pay-other{
    &__inner{
      padding-top: 15.94em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    .text30{
      font-size: 1.25em;
      max-width: 80%;
    }
    &__btn{
      width: 100%;
    }
  }
}
